    scroll-behavior: smooth;
}
body
{
    font-family: "Roboto", sans-serif;
    
}
h1 
{
    font-size: 60px; 
}
@media (min-width: 768px) and (max-width: 991.98px) 
{
    h1 
    {
        font-size: 48px; 
    } 
}
@media (max-width: 767.98px) 
{
    h1 
    {
        font-size: 36px; 
    } 
}
  
h2 
{
    font-size: 42px; 
}
@media (max-width: 767.98px) 
{
    h2 
    {
        font-size: 30px; 
    } 
}
  
h3 
{
    font-size: 30px; 
}
@media (max-width: 767.98px) 
{
    h3 
    {
        font-size: 24px; 
    } 
}
  
h4 
{
    font-size: 24px; 
}
@media (max-width: 767.98px) 
{
    h4 
    {
        font-size: 20px; 
    } 
}
  
  h5 
{
    font-size: 20px; 
}
@media (max-width: 767.98px) 
{
      h5 {
        font-size: 18px; } 
}
  
h6 
{
    font-size: 16px;
    margin-bottom: 0; 
}

h1,h2,h3,h4,h5,h6 
{
  color: #102039;
  font-family: "Roboto", sans-serif;
  font-weight: 700;
  text-transform: capitalize;
  line-height: 1.2;
  margin-bottom: 0; 
}

p {
    color: #606060;
    margin-bottom: 10px; 
}
/*End of General section*/
/*******************************/
/*Meet Our Experts Section*/
.card-style
{
    box-shadow: 0 5px 20px rgb(0 0 0 / 10%);
    margin-bottom: 30px;
    text-align: center;
    border: 0px;
}


.card_image
{
    position: relative;
    overflow: hidden;
}
.card_image .hover_state
{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}
.card_image .hover_state ul 
{
    position: absolute;
    top: 55%;
    width: 100%;
    text-align: center;
    opacity: 0;
    visibility: hidden;
    padding-left: 0px !important;
    padding-bottom: 0px !important;
    transition: all 0.3s ease-in;
    
    
}
.card_image .hover_state ul li
{
    display: inline;
    margin: 0 2px;
}
.card_image .hover_state ul li a 
{
    color:#fff;
    background-color: #0f758c;
    width: 30px;
    height: 30px;
    border-radius: 5px;
    text-align: center;
    line-height: 30px;
    display: inline-block;
    transition: all 0.3s ease-in;
}
.card_image .hover_state ul li a:hover
{
    background-color: #a80038;
   
}
.card_image .hover_state ul li a i
{
    margin-top: 5px;
}

.card-style:hover .card_image .hover_state ul
{
    opacity: 1;
    top: 50%;
    visibility: visible;
}
/* End of Meet Our Experts Section*/
/***************************************/
/* Start of Credit Section*/


.credit
{
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
    background-repeat: no-repeat;
    background-position: top;
    background-size: 100%;
}

@media (min-width: 1440px ) and ( max-width: 1599.98px){
    .credit {
        background-size: 100% 60%;
    }
    
}

.Counter_area
{
    padding-top: 120px;
    padding-bottom: 90px;
}
.icon_box
{
    text-align: center;
    margin-bottom: 30px;
}
.icon_image img
{
    width: auto;
    margin: 0 auto 20px;
}
.icon_content span {
    color: #102039;
    text-transform: capitalize;
    letter-spacing: 2px;
    font-weight: 500;
    margin-top: 5px;
    display: block;
}
img{
    width: 100%;
    height: auto;
    display: block;
}
.lessons {
    position: relative;
    margin-bottom: 30px;
    border-radius: 5px;
}
.IntroLessons::before
{
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-image: linear-gradient(transparent, black);
}
.IntroLessons:hover .lessons_content .play_button a
{
    color: #a80038;
}
.IntroLessons:hover .lessons_content h3 a
{
    color: #a80038;
}
.IntroLessons
{
    position: relative;
    margin-bottom: 30px;
    border-radius: 5px;
}
.lessons_content
{
    position: absolute;
    left: 20px;
    bottom: 20px;
}
.play_button a 
{
    color: #0f758c;
    background-color: #ffffff;
    font-size: 20px;
    width: 40px;
    height: 40px;
    line-height: 40px;
    text-align: center;
    display: inline-block;
    border-radius: 50%;
    -webkit-transition: all 0.3s ease-in;
    -o-transition: all 0.3s ease-in;
    transition: all 0.3s ease-in;
}
.lessons_content h3 a
{
        color: #ffffff;
        -webkit-transition: all 0.3s ease-in;
        -o-transition: all 0.3s ease-in;
        transition: all 0.3s ease-in;
}
/* End of Credit section*/
/***************************************/
/* feedback */
.single_feedback
{
    margin: 20px 15px;
    background-color: #ffffff;
    position: relative;
    padding: 40px;
    box-shadow: 0 0 10px rgb(0 0 0 / 10%);
}
.quote-icon
{
    position: absolute;
    top: 40px;
    right: 40px;
    font-size: 40px;
    color: rgba(0, 0, 0, 0.4);
}
.reviewer_image img
{
    width: 80px;
    height: 80px;
    border-radius: 50%
}
.reviewer_text h4
{
    color: #0f758c;
    transition: all 0.3s ease-in;
}
.reviewer_text span
{
    text-transform: capitalize;
    display: block;
    margin-top: 5px;
}
/* end of feedback */
/***************************************/
/* blog */
.blog_section
{
    background-color: #fffcf3;
    padding-top: 115px;
    padding-bottom: 100px;
}
.blog_section .blog_slider .slick-slide 
{
    margin: 20px 15px;
}
.blog_image img
{
    border-radius: 5px 5px 0 0;
}
.blog_content
{
    padding: 30px;
    background-color: #ffffff;
    box-shadow: 0 3px 20px rgb(0 0 0 / 10%);
    border-radius: 0 0 5px 5px;
}
.blog_title h4 a
{
    color: #102039;
    transition: all 0.3s ease-in;
}
.blog_title h4 a:hover
{
    color: #a80038;
}
.content_wrap
{
    display: flex;
    align-items: center;
    margin: 10px 0 20px;
}
.content_wrap a 
{
    color: #0f758c;
    text-transform: capitalize;
    transition: all 0.3s ease-in;
    
}
a
{
    text-decoration: none !important;
}
.content_wrap a + a
{
    margin-left: 30px;
}
.button_orange
{
    
    color: #0f758c;
    background-color: transparent;
    border: 1px solid #0f758c;
    font-size: 16px;
    font-weight: 500;
    text-transform: capitalize;
    text-align: center;
    display: inline-block;
    padding: 14px 40px;
    transition: all 0.3s ease-in;
    border-radius: 5px;
}
.button_orange:hover
{
    color: #ffffff;
    background-color: #a80038;
    border: 1px solid transparent;
}
/* end of blog */
/***************************************/
/* Footer section*/
.footer_image
{
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_1___});
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;

      
}
.card_footer
{
    padding-top: 115px;
}
.footer_title h5
{
    color: #fff !important;
    margin-bottom: 20px;
}
.widget p
{
    color: rgba(234, 234, 234, 0.7);;
}
.widget ul 
{
    padding-left: 0px ;
}
.widget ul li
{
    margin-top: 10px;
}
.widget ul li 
{
    color: rgba(234, 234, 234, 0.7) ;
    display: block;
}
.widget ul li a
{
    color: rgba(234, 234, 234, 0.7);
}

.widget ul li:hover a
{
    color: #a80038 !important;
}
.schedule
{
    margin-bottom: 40px;
}
.schedule ul li 
{
    display: flex;
    justify-content: space-between;
}
.dividor
{
    width: 100%;
    margin-top: 10px;
    margin-bottom: 10px;
    margin-left: auto;
    margin-right: auto;
    height: 1px;
    background-color: rgba(234, 234, 234, 0.7);
}
.copy_right
{
    border-top: 1px solid rgba(234, 234, 234, 0.7);
    color: rgba(234, 234, 234, 0.7);
    padding-top: 20px;
    padding-bottom: 20px;
}
.copy_right span a 
{
    color: #a80038;
}
.footer_copyright_links
{
    text-align: right;
}
.footer_copyright_links ul
{
    margin-bottom: 0px;
}
.footer_copyright_links ul li
{
    display: inline;
    margin-left: 15px;
}
.footer_copyright_links ul li a
{
    color: rgba(234, 234, 234, 0.7);
}
.footer_copyright_links ul li:hover a
{
    color: #a80038;
}
/*End of Footer section*/
.BackToTop 
{
    position: fixed;
    height: 50px;
    width: 50px;
    bottom: 50px;
    right: 50px;
    background-color: #0f758c;
    color: #ffffff;
    font-size: 30px;
    font-weight: 700;
    line-height: 50px;
    border-radius: 5px;
    text-align: center;
    cursor: pointer;
    display: none;
    z-index: 10;
    transition: all 0.3s ease-in;
    visibility: hidden;
}
@media (max-width : 992px)
{
    .BackToTop
    {
        display: none !important;
    }
    
}
.BackToTop:hover
{
    background-color: #a80038;
}
.Visiable{
    visibility: visible;
}

